body{
    background:linear-gradient(to left, red, teal, red) ;

}

.MainContainer{
    height: 99vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.BoxContainer{
    color: white;
    border: 2px solid orange;
    box-shadow: 0 2px 100px orange;
    /* padding:200px; */
}

.heart{
    position: relative;
    margin:20px;
    height: 170px;
    width: 200px;
    border: 2px solid white;

}

.heart::before,
.heart::after{
    position: absolute;
    content:"";
    width: 100px;
    height: 160px;
    background: red;

}

.heart::before{
    left:100px;
}


.heart::after{

}

.sow_img{
    height:250px;
    width:150px;
    margin-bottom: 20px;

}
